<template>
  <el-dialog
    title=""
    :visible.sync="attrConfigDialog"
    width="1000px"
    :close-on-click-modal="false"
    @close="handleClose"
    @open="initTagAttrsList"
  >
    <div><span style="color: red">*</span> </div>
    <el-form
      class="attrConfigForm"
      :model="attrConfigForm"
      :rules="attrConfigRule"
      ref="attrConfigRef"
      :inline="true"
      :hide-required-asterisk="true"
    >
      <el-form-item label="key" prop="attrKey">
        <el-input v-model="attrConfigForm.attrKey" style="width: 100px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="attrName">
        <el-input v-model="attrConfigForm.attrName" style="width: 100px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="attrType">
        <el-select v-model="attrConfigForm.attrType" placeholder="" @change="handleTypeChange" style="width: 120px">
          <el-option
            v-for="item in attrConfigTypes"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="defValue">
        <el-radio-group v-if="showDefaultValItem(1)" v-model="attrConfigForm.defValue" style="width: 168px">
          <el-radio label="1"></el-radio>
          <el-radio label="0"></el-radio>
        </el-radio-group>
        <el-input v-if="showDefaultValItem(2)" v-model="attrConfigForm.defValue" @input="handleIntInput" style="width: 168px"></el-input>
        <el-time-picker
          v-if="showDefaultValItem(3)"
          v-model="attrConfigForm.defValue"
          placeholder=""
          format="HH:mm"
          value-format="HH:mm"
          :editable="false"
          style="width: 168px"
        >
        </el-time-picker>
        <el-input
          v-if="showDefaultValItem(4)"
          v-model="attrConfigForm.defValue"
          @input="handleVolumeInput"
          style="width: 168px"
        >
        </el-input>
        <el-input v-if="showDefaultValItem(5)" v-model="attrConfigForm.defValue" style="width: 168px"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="handleAddAttr"></el-button>
      </el-form-item>
    </el-form>
    <!--  -->
    <div><span style="color: red">*</span> </div>
    <el-table :data="attrConfigData" style="width: 100%; margin-top: 30px;">
      <el-table-column prop="attrId" label="ID" width="80"></el-table-column>
      <el-table-column prop="attrKey" label="key"></el-table-column>
      <el-table-column prop="attrName" label=""></el-table-column>
      <el-table-column prop="defValue" label=""></el-table-column>
      <el-table-column prop="attrType" label="" :formatter="typeFormatter"></el-table-column>
      <el-table-column prop="createrUserName" label="" width="200"></el-table-column>
      <el-table-column label="" width="80">
        <template slot-scope="scope">
          <span v-if="!scope.row.canDelFlag" class="disableDel"></span>
          <el-popconfirm
            v-if="scope.row.canDelFlag"
            confirm-button-text=''
            cancel-button-text=''
            @confirm="handleAttrDel(scope.row)"
            icon="el-icon-info"
            icon-color="red"
            title=""
          >
            <span slot="reference" class="enableDel"></span>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
    <span slot="footer" class="dialog-footer">
      <el-button @click="compete" type="primary"></el-button>
    </span>
  </el-dialog>
</template>

<script setup>
import { reactive, ref, defineProps, defineEmits, computed, onMounted, getCurrentInstance } from 'vue'
import { getTagAttrsList, addTagAttrs, delTagAttrs } from '@/services/attr.js'

// vue proxy.$message
const { proxy } = getCurrentInstance()

const props = defineProps({
  value: {
    type: Boolean
  },
  serId: {
    type: Number
  }
})

const emit = defineEmits(['input'])

const attrConfigDialog = computed({
  get () {
    return props.value
  },
  set (newVal) {
    console.log(newVal, 'set')
    emit('input', newVal)
  }
})

const attrConfigForm = reactive({
  attrKey: '', // key
  attrName: '', // 
  attrType: 2, // 
  defValue: '' // 
})

// attrConfigDOM
const attrConfigRef = ref(null)

// 
const attrConfigRule = {
  attrName: [
    { required: true, message: '', trigger: 'blur' }
  ],
  attrType: [
    { required: true, message: '', trigger: 'blur' }
  ],
  attrKey: [
    { required: true, message: 'key', trigger: 'blur' }
  ]
}

const attrConfigTypes = [
  {
    value: 1,
    label: ''
  },
  {
    value: 2,
    label: ''
  },
  {
    value: 3,
    label: ''
  },
  {
    value: 4,
    label: ''
  },
  {
    value: 5,
    label: ''
  }
]

const attrConfigData = ref([])

const initTagAttrsList = async () => {
  console.log(props.serId)
  console.log('')
  // 
  const { data } = await getTagAttrsList({ serId: props.serId })
  attrConfigData.value = data
  console.log(data, 'data')
}

//  - 
const handleAddAttr = async () => {
  const valid = await attrConfigRef.value.validate().catch(() => false)
  if (!valid) return null
  const data = await addTagAttrs({ serviceId: props.serId, ...attrConfigForm })
  console.log(data)
  if (data.result === 'success') {
    proxy.$message({
      message: '',
      type: 'success',
      duration: 1000
    })
    // 
    attrConfigRef.value.resetFields()
    // 
    initTagAttrsList()
  } else {
    proxy.$message({
      message: data.msg,
      type: 'error',
      duration: 1000
    })
  }
}

// 
const handleAttrDel = async (row) => {
  console.log('', row.attrId)
  const data = await delTagAttrs({ id: row.attrId })
  if (data.result === 'success') {
    proxy.$message({
      message: '',
      type: 'success',
      duration: 1000
    })
    // 
    initTagAttrsList()
  } else {
    proxy.$message({
      message: data.msg,
      type: 'error',
      duration: 1000
    })
  }
}

// 
const compete = () => { attrConfigDialog.value = false }

// form-itemdefValform-itemtime-picker
const showDefaultValItem = computed(() => {
  return function (type) {
    return attrConfigForm.attrType === type
  }
})

// 
const handleVolumeInput = (value) => {
  const reg = /^(100|[1-9]\d|\d)$/ // 
  if (!reg.test(value)) {
    attrConfigForm.defValue = value.replace(/[^\d]/g, '') // ''
    if (+attrConfigForm.defValue > 100) {
      attrConfigForm.defValue = '100'
    }
  }
}

// 
const handleIntInput = (value) => {
  const reg = /^-?\d+$/ // 
  if (!reg.test(value)) {
    attrConfigForm.defValue = value.replace(/[^\d-]/g, '') // ''
  }
}

// 
const handleClose = (formName) => {
  attrConfigRef.value.resetFields()
  attrConfigRef.value.clearValidate()
}

// 
const typeFormatter = (row) => {
  return attrConfigTypes.find((option) => option.value === row.attrType).label
}

// 
const handleTypeChange = () => {
  // 
  attrConfigForm.defValue = ''
}

</script>

<style lang="stylus" scoped>
.attrConfigForm {
  display flex
  justify-content center
  margin 20px
  .el-form-item {
    margin-right 18px
  }
}
.enableDel {
  color #1890ff
  cursor pointer
}
.disableDel{
  color #d1d1d1
  cursor not-allowed
}
.el-table /deep/ th.el-table__cell {
  background #f1f3f4
}

</style>
