<template>
  <el-dialog
    :title="diglogConfig.title"
    :visible.sync="FormDialog"
    width="900px"
    @open="initServeData"
    @close="handleClose"
    :close-on-click-modal="false"
  >
    <el-form
      class="form"
      :model="form"
      :rules="rules"
      ref="formRef"
      label-width="80px"
    >
      <el-form-item label="" prop="serviceName">
        <el-input v-model="form.serviceName" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="ID" prop="serviceKey">
        <el-input v-model="form.serviceKey" placeholder=""></el-input>
      </el-form-item>
      <el-form-item class="upload-icon" label="" prop="imageUrl">
        <el-upload
          action="#"
          :http-request="handleIconUpload"
          name="icon"
          list-type="picture-card"
          :file-list="fileList"
          :onbefore-upload="beforeIconUpload"
          :on-preview="handleIconPreview"
          :on-remove="handleIconRemove"
          :on-exceed="handleUploadExceed"
          :limit="1"
        >
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible" append-to-body>
          <img width="100%" :src="dialogImageUrl" alt="">
        </el-dialog>
      </el-form-item>
      <el-form-item label="" prop="remark">
        <el-input type="textarea" :rows="3" placeholder="" v-model="form.remark"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="handleComplete"></el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>

<script setup>
import { reactive, ref, defineProps, defineEmits, computed, getCurrentInstance } from 'vue'
import { getServiceAPI, uploadServiceIcon, updateServiceAPI, addServicesAPI } from '@/services/services.js'

// vue proxy.$message
const { proxy } = getCurrentInstance()

const props = defineProps({
  value: {
    type: Boolean
  },
  diglogConfig: {
    type: Object,
    default: () => ({})
  }
})

const emit = defineEmits(['input', 'updateServeList'])

const FormDialog = computed({
  get () {
    return props.value
  },
  set (newVal) {
    emit('input', newVal)
  }
})

// 
const form = ref({
  serviceName: '', // 
  serviceKey: '', // 
  imageUrl: '', // 
  remark: '' // 
})

// 
const fileList = ref([])

// icondiglogicon
const dialogVisible = ref(false)
const dialogImageUrl = ref('')

// serviceKey
const validateEven = (rule, value, callback) => {
  const reg = /^[a-zA-Z][a-zA-Z0-9_]*$/
  if (value && !reg.test(value)) {
    callback(new Error(''))
  } else {
    callback()
  }
}

// 
const rules = {
  serviceName: [
    { required: true, message: '', trigger: 'blur' },
    { max: 50, message: '50' }
  ],
  serviceKey: [
    { required: true, message: 'ID', trigger: 'blur' },
    { validator: validateEven, trigger: 'blur' }
  ],
  imageUrl: [
    { required: true, message: '', trigger: 'blur' }
  ]
}

// formRefdiglog
const formRef = ref(null)

// 
const handleClose = () => {
  formRef.value.resetFields()
  formRef.value.clearValidate()
  fileList.value = []
}

// 
const initServeData = async () => {
  // 
  console.log(props.diglogConfig)
  if (props.diglogConfig.diglogType === 'edit') {
    // 
    const { data } = await getServiceAPI({ id: props.diglogConfig.serId })
    form.value.serviceName = data.serviceName
    form.value.serviceKey = data.serviceKey
    form.value.remark = data.remark
    form.value.imageUrl = data.imageUrl
    data.imageUrl && fileList.value.push({ name: 'icon', url: data.imageUrl })
  }
}

// 
const handleIconUpload = async ({ file }) => {
  console.log('')
  const fileObj = new FormData()
  fileObj.append('file', file)
  // url
  const res = await uploadServiceIcon(fileObj)
  if (res.result === 'success') {
    form.value.imageUrl = res.data
    console.log(fileList.value)
  } else {
    fileList.value = []
  }
}

//     
const handleComplete = async () => {
  const valid = await formRef.value.validate().catch(() => false)
  if (!valid) return null
  const { diglogType, serId } = props.diglogConfig
  if (diglogType === 'edit') {
    // 
    const data = await updateServiceAPI({ ...form.value, id: serId })
    if (data.result === 'success') {
      proxy.$message({
        message: '',
        type: 'success',
        duration: 1000
      })
      // 
      FormDialog.value = false
    } else {
      proxy.$message({
        message: data.msg,
        type: 'error',
        duration: 1000
      })
    }
  } else if (diglogType === 'add') {
    // 
    const data = await addServicesAPI({ ...form.value })
    if (data.result === 'success') {
      proxy.$message({
        message: '',
        type: 'success',
        duration: 1000
      })
      // 
      FormDialog.value = false
      // 
      emit('updateServeList')
    } else {
      proxy.$message({
        message: data.msg,
        type: 'error',
        duration: 1000
      })
    }
  }
}

// icon
const beforeIconUpload = (file) => {
  // console.log(file)
  // let typeLimit = file.type
}

// icon
const handleIconRemove = (file, list) => {
  fileList.value = []
  form.value.imageUrl = ''
}

// 
const handleUploadExceed = () => {
  proxy.$message({
    type: 'error',
    message: '',
    duration: 1000
  })
}

// 
const handleIconPreview = (file) => {
  dialogImageUrl.value = file.url
  dialogVisible.value = true
}
</script>

<style lang="stylus" scoped>
.form
  width: 700px
  .upload-icon
    /deep/ .el-form-item__content > div:first-child
      display flex
      .el-upload-list
        order 1
        margin-left 20px
        height: 148px
</style>
